@use '../../../assets/styles/abstracts/colors' as colors;

.startup {

  &__lead {
    padding: 100px 0;

    &-container {
      display: flex;
      justify-content: space-between;
    }

    &-content-container {
      max-width: 523px;
      margin-right: 80px;
    }

    &-title {
      margin: 40px 0 40px;
      padding: 0;
    }

    &-link {
      display: block;
      font-size: 24px;
      line-height: 29px;
      color: colors.$blue;
      margin: 40px 0 0;
      text-transform: lowercase;

      &::after {
        display: inline-block;
        content: '\1F783';
        font-size: 18px;
        font-weight: 700;
        transform: translateY(-2px);
        margin-left: 10px;
      }
    }

    &-image-container {
      width: 100%;
      position: relative;
      display: flex;
      justify-content: center;
      align-items: center;
    }

    &-image-wrapper {
      min-width: 100%;
      max-width: 100%;
    }

    &-image {
      width: 100%;
      object-fit: contain;
    }

    &-button {
      position: absolute;
      bottom: 0;
      font-size: 24px;
      line-height: 29px;
      font-weight: 400;
      padding: 25px 28px;
      color: white;
      text-decoration: none;
      background: linear-gradient(90.81deg, #7185EC 23.55%, #324BCD 84.6%);
      box-shadow: 0px 3px 6px #000000;
      border-radius: 16px;
      white-space: nowrap;
      
      &::before {
        content: '\271A';
        display: inline-block;
        color: white;
        margin-right: 10px;
        border: none;
      }
    }
  }

  &__content {
    padding: 200px 0;

    &-container {
      display: flex;
      justify-content: space-between;
    }

    &-text-container {
      max-width: 984px;
      margin-right: 36px;
    }

    &-title {
      margin: 0 0 60px;

      &_last {
        text-transform: uppercase;
        margin-bottom: 20px;
      }
    }

    &-flexbox-container {
      display: flex;
      justify-content: space-between;
      gap: 110px;
      margin-bottom: 200px;
    }

    &-flexbox {
      width: 50%;
    }

    &-subtitle {
      font-size: 32px;
      line-height: 39px;
      font-weight: 700;
      letter-spacing: -0.02em;
      color: #222222;
      margin: 0 0 20px;
      padding: 0;
      text-transform: lowercase;
    }

    &-text {
      font-size: 18px;
      line-height: 22px;
      margin: 0 0 20;
      padding: 0;
      color: #222222;
      max-width: 690px;

      &:last-of-type {
        margin-bottom: 0;
      }
    }

    &-contacts-container {
      max-width: 384px;
    }

    &-contacts {
      color: rgba(255, 255, 255, 0.87);
      background: #1D1D1D;
      border-radius: 4px;
      padding: 36px 50px;

      &-title {
        font-size: 32px;
        line-height: 39px;
        font-weight: 700;
        letter-spacing: -0.02em;
        text-transform: lowercase;
        margin: 0 0 20px;
        padding: 0;
      }

      &-list {
        list-style: none;
        margin: 0;
        padding: 0;

        &-item {
          font-size: 18px;
          line-height: 22px;
          margin-bottom: 20px;
        }
      }

      &-link {
        display: block;
        color: colors.$blue;
        text-decoration: none;
      }
    }
  }
}